import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch } from "react-redux";
import { useLocation } from "react-router-dom";
import { get_order_products } from "../../redux/actions/orders";
import Product from "../Product/Product";
import { buttons } from "./OrderModalButtons";

function OrderModal({ choosedOrder }) {
	const { t } = useTranslation(["account", "form"]);
	const closeModalRef = React.createRef();
	const [orderProducts, setOrderProducts] = useState([]);
	const dispatch = useDispatch();
	const location = useLocation();

	useEffect(() => {
		dispatch(get_order_products(choosedOrder?.id)).then((res) => {
			setOrderProducts(res.data);
		});
	}, [choosedOrder, dispatch]);
	useEffect(() => {
		closeModalRef?.current?.click();
	}, [location]);
	return (
		<div
			class="modal fade"
			id="exampleModal3"
			tabIndex="-1"
			aria-labelledby="exampleModalLabel"
			aria-hidden="true"
		>
			<div
				style={{ borderRadius: "30px", border: "0" }}
				class="modal-dialog  modal-dialog-scrollable modal-fullscreen-sm-down"
			>
				<div class="modal-content container " style={{ background: "#F2F5F7" }}>
					<div
						class="modal-header border-0 my-2 bg-white"
						style={{
							borderRadius: "15px",
							boxShadow: "0 10px 30px 0 rgba(68, 55, 77, 0.2)",
						}}
					>
						<h3 class="modal-title" id="exampleModalLabel">
							{t("account:Order")} #{choosedOrder?.id}
						</h3>
						<button
							type="button"
							class="close-btn btn"
							data-bs-dismiss="modal"
							ref={closeModalRef}
							aria-label="Close"
							style={{ borderRadius: "15px" }}
						>
							<i class="bi bi-x-lg"></i>
						</button>
					</div>
					<div class="modal-body ">
						{/* Top */}
						{buttons.map((but) => {
							return (
								<div
									className=" mx-2 h4 btn btn-primary"
									style={{
										borderRadius: "15px",
										boxShadow: "0 10px 30px 0 rgba(68, 55, 77, 0.1)",
									}}
									data-bs-toggle="collapse"
									data-bs-target={`#${but.id}`}
									aria-expanded="false"
									aria-controls={but.id}
								>
									{t(`form:${but.name}`)}
								</div>
							);
						})}
						<hr />
						{/* Content */}
						{buttons.map((btn) => {
							return (
								<div
									id={btn.id}
									className="row collapse align-items-center justify-content-between my-3"
								>
									<h4 className="col-6">{t(`form:${btn.name}`)}:</h4>{" "}
									<div class="col-6">
										{btn.type === "textarea" ? (
											<textarea
												style={{ borderRadius: "15px" }}
												type="text"
												disabled={true}
												class="form-control w-100"
												placeholder="Nothing"
												value={choosedOrder && choosedOrder[btn?.value]}
												aria-label="Username"
												aria-describedby="addon-wrapping"
											></textarea>
										) : (
											<input
												style={{ borderRadius: "15px" }}
												type="text"
												disabled={true}
												class="form-control w-100"
												placeholder="Nothing"
												value={choosedOrder && choosedOrder[btn?.value]}
												aria-label="Username"
												aria-describedby="addon-wrapping"
											/>
										)}
									</div>
								</div>
							);
						})}
						<div role="button" className="h3 text-center my-4">
							{t(`account:Products`)} ({choosedOrder?.total_products})
						</div>

						<div
							onClick={() => closeModalRef?.current?.click()}
							className="row  row-cols-1 row-cols-sm-2  my-4"
						>
							{Array.isArray(orderProducts) &&
								orderProducts.map((product) => {
									return (
										<Product
											type="order"
											cls="mt-md-4"
											size={product.size}
											orderPrice={product.price}
											orderQuantity={product.quantity}
											item={product.product}
										/>
									);
								})}
						</div>
					</div>
				</div>
			</div>
		</div>
	);
}

export default OrderModal;
