import React from "react";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import Main from "../components/Account/Main";
import Sidebar from "../components/Account/Sidebar";
import BigHeader from "../components/Products/BigHeader";
import Breadcumb from "../components/Products/Breadcumb";

const mapState = ({ auth }) => ({
	user: auth.user,
});

function Account() {
	const { user } = useSelector(mapState);
	const { t } = useTranslation(["account"]);
	// if (!isAuthenticated) return <Redirect to="/login" />;
	return (
		<div>
			<BigHeader searchedCategoryName={t("account:My_account")} />

			<div className="container">
				<Breadcumb
					searchedCategoryName={t("account:My_account")}
					topmar={true}
				/>

				<div className="row py-5">
					<Sidebar user={user} />

					<Main />
				</div>
			</div>
		</div>
	);
}

export default Account;
