import React, { useEffect } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import { get_account_info } from "../../redux/actions/account";
import { selectAccountData } from "../../redux/selectors/auth";
import { selectWishItems } from "../../redux/selectors/wishlist";
import { accountData } from "./AccountInfoData";

const mapState = (state) => ({
	wishItems: selectWishItems(state),
	account_data: selectAccountData(state),
	user: state.auth.user,
});

function AccountInfo() {
	const dispatch = useDispatch();
	const { t } = useTranslation(["account", "common"]);
	const { wishItems, account_data, user } = useSelector(mapState);
	useEffect(() => {
		dispatch(get_account_info());
	}, [dispatch]);
	return (
		<div className="row ">
			<div className="col-12 my-2 mb-4">
				<h2>{t("account:account_information")}</h2>
			</div>
			{accountData.map((column) => {
				return (
					<div
						className={`col-11 col-md-5 truncate-overflow-1 border mx-auto ${column.classes}  my-1  py-4 m-md-2`}
						style={{
							borderRadius: "15px",
							...(column.styles ? { ...column.styles } : {}),
						}}
					>
						<div className="d-flex flex-column container p-0 px-md-3">
							<div>{t(`account:${column.transName}`)}</div>
							<div className="h2 py-1" style={{ fontWeight: 700 }}>
								{account_data
									? `${account_data[column.value]} ${
											column.type === "shop" ? t("common:manat") : ""
									  }`
									: `${t("common:loading")}...`}
							</div>
						</div>
					</div>
				);
			})}

			<div
				className="col-11 col-md-5 truncate-overflow-1 border  bg-danger mx-auto text-white py-4 m-md-2"
				style={{ borderRadius: "15px" }}
			>
				<div className="d-flex flex-column container">
					<div>{t("account:liked_products_count")}</div>
					<div className="h2 py-1" style={{ fontWeight: 700 }}>
						{wishItems && wishItems.length}
					</div>
				</div>
			</div>
			<div
				className="col-11 col-md-5 truncate-overflow-1 border  bg-danger mx-auto text-white py-4 m-md-2"
				style={{ borderRadius: "15px" }}
			>
				<div className="d-flex flex-column container">
					<div>{t("account:login_time")}</div>
					<div className="h4 py-1" style={{ fontWeight: 700 }}>
						{user && user.date_joined}
					</div>
				</div>
			</div>
		</div>
	);
}

export default AccountInfo;
