import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch } from "react-redux";
import { delete_orders, get_orders } from "../../redux/actions/orders";
import { ADD_MESSAGE } from "../../redux/types/types";
import OrderModal from "./OrderModal";

function OrderTab() {
	const { t } = useTranslation(["common", "account", "cities"]);
	const [result, setResult] = useState(null);
	const dispatch = useDispatch();
	const [choosedOrder, setChoosedOrder] = useState([]);
	const openModalRef = React.createRef();
	const [modalOrder, setModalOrder] = useState(null);
	const [choosedAll, setChoosedAll] = useState(false);
	const [filterOrder, setFilterOrder] = useState(null);
	const [loadMore, setLoadMore] = useState(null);
	const [resProducts, setResProducts] = useState(null);

	// Toggling order list
	const handleFilterChange = (id) => {
		if (choosedOrder.find((foundId) => foundId === id)) {
			handelChoosedOrderRemove(id);
		} else {
			handelChoosedOrderAdd(id);
		}
	};

	const handleFilterOrders = (key) => {
		setFilterOrder({ key: key });
	};
	// Load More
	const handleLoadMore = () => {
		setLoadMore(resProducts.length);
	};
	// Adding or Removing all orders to/from list
	const handleAddAll = () => {
		if (choosedOrder.length === result?.data?.results.length) {
			let choosed = [];
			setChoosedOrder([...choosed]);
			setChoosedAll(false);
		} else {
			let choosed = result?.data?.results.map((order) => {
				return order?.id;
			});
			setChoosedOrder([...choosed]);
			setChoosedAll(true);
		}
	};
	// Open order Modal
	const handleOpenModal = (tookenOrder) => {
		setModalOrder(tookenOrder);
		if (modalOrder) {
			openModalRef.current.click();
		}
	};

	// Delete choosed orders
	const handleDelete = () => {
		dispatch(delete_orders(choosedOrder)).then((res) => {
			if (res?.status === 200) {
				dispatch({ type: ADD_MESSAGE, payload: "Order(s) was deleted" });
				setChoosedOrder([]);
				getOrders();
			} else {
				// console.log("Error");
			}
		});
	};
	// Adding order to list
	const handelChoosedOrderAdd = (id) => {
		setChoosedOrder([...choosedOrder, id]);
	};
	// Removing order from list
	const handelChoosedOrderRemove = (id) => {
		const reduced = choosedOrder.filter((choosedOrde) => choosedOrde !== id);
		setChoosedOrder([...reduced]);
	};
	// Get Api config to fetch orders
	const getConfi = () => {
		const confi = `${filterOrder?.key ? `ordering=${filterOrder.key}` : ""}${
			loadMore ? `&limit=20&offset=${loadMore}` : ""
		}`;
		return confi;
	};

	// Getting orders from database
	const getOrders = () => {
		const confi = getConfi();
		dispatch(get_orders(confi)).then((res) => {
			setResult(res);
			if (loadMore) {
				setResProducts([...resProducts, ...res.data.results]);
			} else {
				setResProducts(res?.data?.results);
			}
		});
	};
	// After filter change get new orders
	useEffect(() => {
		getOrders();
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [filterOrder, loadMore]);

	return (
		<>
			<OrderModal choosedOrder={modalOrder} />
			<div className="row ">
				<div
					ref={openModalRef}
					data-bs-toggle="modal"
					data-bs-target="#exampleModal3"
				></div>
				<div className="d-flex justify-content-between p-2 align-items-center">
					<div className="d-flex align-items-center">
						<div className="h2 ">{t("account:Orders")} </div>
						<div className="h4 ms-3 ">
							({result?.data?.count} {t("account:orders")})
						</div>
					</div>
					<button
						className="btn btn-danger py-2 px-3"
						style={{ borderRadius: "25px" }}
						onClick={() => handleDelete()}
						disabled={choosedOrder.length < 1}
					>
						{t("account:Delete")}
					</button>
				</div>
				<div
					className="my-2 bg-white py-3 table-responsive-md"
					style={{
						borderRadius: "30px",
						boxShadow: "0 10px 30px 0 rgba(68, 55, 77, 0.25)",
					}}
				>
					<table class="table ">
						<thead style={{ borderBottom: "none" }}>
							<tr className="border-0">
								<th scope="col">
									<div class="form-check">
										<input
											class="form-check-input"
											type="checkbox"
											value=""
											id="ID"
											checked={choosedAll}
											onChange={() => handleAddAll()}
										/>
										<label
											class="form-check-label"
											onClick={() => {
												filterOrder?.key === "-id"
													? handleFilterOrders("id")
													: handleFilterOrders("-id");
											}}
											role="button"
										>
											Id
											{filterOrder?.key === "id" && (
												<i class="ps-1 bi bi-caret-down-fill"></i>
											)}
											{filterOrder?.key === "-id" && (
												<i class="ps-1 bi bi-caret-up-fill"></i>
											)}
										</label>
									</div>
								</th>

								<th
									scope="col"
									role="button"
									onClick={() => {
										filterOrder?.key === "-created"
											? handleFilterOrders("created")
											: handleFilterOrders("-created");
									}}
								>
									{t("account:Created")}{" "}
									{filterOrder?.key === "created" && (
										<i class="ps-1 bi bi-caret-down-fill"></i>
									)}
									{filterOrder?.key === "-created" && (
										<i class="ps-1 bi bi-caret-up-fill"></i>
									)}
								</th>

								<th
									scope="col"
									onClick={() => {
										filterOrder?.key === "-city"
											? handleFilterOrders("city")
											: handleFilterOrders("-city");
									}}
									role="button"
								>
									{t("account:City")}{" "}
									{filterOrder?.key === "city" && (
										<i class="ps-1 bi bi-caret-down-fill"></i>
									)}
									{filterOrder?.key === "-city" && (
										<i class="ps-1 bi bi-caret-up-fill"></i>
									)}
								</th>
								<th
									scope="col"
									role="button"
									onClick={() => {
										filterOrder?.key === "-total_shop"
											? handleFilterOrders("total_shop")
											: handleFilterOrders("-total_shop");
									}}
								>
									{t("account:Total")}{" "}
									{filterOrder?.key === "total_shop" && (
										<i class="ps-1 bi bi-caret-down-fill"></i>
									)}
									{filterOrder?.key === "-total_shop" && (
										<i class="ps-1 bi bi-caret-up-fill"></i>
									)}
								</th>
								<th scope="col">{t("account:Products")} </th>
								<th scope="col">{t("account:Open")} </th>
							</tr>
						</thead>
						<tbody>
							{Array.isArray(resProducts) &&
								resProducts.length > 0 &&
								resProducts.map((order) => {
									return (
										<>
											<tr classNames="py-3">
												<th scope="row">
													<div class="form-check">
														<input
															class="form-check-input"
															type="checkbox"
															value=""
															id={order?.id}
															onChange={() => handleFilterChange(order?.id)}
															checked={choosedOrder.includes(order.id)}
														/>
														<label class="form-check-label" for={order?.id}>
															{order?.id}
														</label>
													</div>
												</th>
												<td>{order?.created}</td>
												<td>{t(`cities:${order?.city_display}`)}</td>
												<td>
													<strong>{order?.total_shop} </strong>
													<span className="small d-none d-md-inline">
														{t("common:manat")}
													</span>
												</td>
												<td className="text-center">{order?.total_products}</td>
												<td>
													<button
														onClick={() => handleOpenModal(order)}
														className="btn btn-outline-success border-0"
														style={{ borderRadius: "30px" }}
													>
														<i class="bi bi-box-arrow-in-up-right"></i>
													</button>
												</td>
											</tr>
										</>
									);
								})}
						</tbody>
					</table>
				</div>
				{result?.data?.next && (
					<div className="row my-4 justify-content-center">
						<button
							className="btn btn-primary "
							style={{
								width: "500px",
								borderRadius: "30px",
								fontWeight: "600 !important",
								fontSize: "18px",
							}}
							onClick={() => handleLoadMore()}
						>
							{t("account:Load More")}
						</button>
					</div>
				)}
			</div>
		</>
	);
}

export default OrderTab;
