import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { validateProfileInfo } from "../Validate/validateOrder";
import isEmpty from "../Validate/isEmpty.jsx";
import { update_profile } from "../../redux/actions/auth";
import { ADD_ERROR, ADD_MESSAGE } from "../../redux/types/types";
import { useTranslation } from "react-i18next";
const mapState = (state) => ({
	user: state.auth.user,
});

function ProfileTab() {
	const { t } = useTranslation(["account", "cities"]);
	const { user } = useSelector(mapState);
	const [formInputs, setFormInputs] = useState({
		username: user?.username,
		phoneNumber: user?.phone_number,
		email: user?.email ? user.email : "",
		city: user?.city ? user?.city : "Ag",
		address: user?.address,
	});
	const [formErrors, setFormErrors] = useState(null);
	const [file, setFile] = useState(null);
	const [showImage, setShowImage] = useState(user?.profile_social);
	const dispatch = useDispatch();

	const handleChange = (e) => {
		const value = e.target.value;
		const name = e.target.name;
		setFormInputs({ ...formInputs, [name]: value });
	};
	const [submitted, setSubmitting] = useState(false);

	useEffect(() => {
		setFormErrors(validateProfileInfo(formInputs));
	}, [formInputs]);

	const handleImageChange = (e) => {
		if (e.target.files[0]) {
			setFile(e.target.files[0]);
			setShowImage(URL.createObjectURL(e.target.files[0]));
			// setFile(URL.createObjectURL(e.target.files[0]));
		}
	};

	const handleSubmit = (e) => {
		e.preventDefault();
		setFormErrors(validateProfileInfo(formInputs, true));
		if (isEmpty(formErrors)) {
			setSubmitting(true);
			dispatch(update_profile(formInputs, file)).then((result) => {
				setSubmitting(false);
				if (result.status === 200) {
					dispatch({
						type: ADD_MESSAGE,
						payload: "Profile successfully updated!",
					});
				} else if (result.message === "Request failed with status code 400") {
					// console.log("errror", result.message);
					dispatch({
						type: ADD_ERROR,
						payload: "Username, phone number or email already have been taken!",
					});
				} else {
					dispatch({
						type: ADD_ERROR,
						payload: result.message,
					});
				}
			});
		} else {
			// console.log("error", formErrors);
		}
	};

	return (
		<div className="row">
			<div className="col-12 h2 my-3 ps-4">{t("account:Profile")}</div>
			<div className=" col-12 d-inline-flex justify-content-center position-relative">
				<img
					className="img-fluid mb-2"
					style={{ borderRadius: "30px", maxHeight: "300px" }}
					src={showImage}
					alt="Avatar"
				/>
			</div>
			<div className="col-12 my-5 ">
				<form className="row" onSubmit={(e) => handleSubmit(e)}>
					<div className=" col-12 row justify-content-center mb-3">
						<div className="col-md-6">
							<input
								class="form-control "
								type="file"
								accept="image/png, image/jpeg, image/webp"
								style={{ borderRadius: "15px" }}
								onChange={(e) => handleImageChange(e)}
							/>
						</div>
					</div>
					<div className="mb-2 col-6 form-floating">
						<input
							type="text"
							class={`form-control ${
								formErrors?.username ? "is-invalid" : "is-valid"
							}`}
							id="nameInput"
							name="username"
							value={formInputs.username}
							autoFocus={true}
							onChange={(e) => handleChange(e)}
							placeholder="Full Name"
							style={{ borderRadius: "20px" }}
						/>
						<label for="nameInput" style={{ left: "10px" }}>
							{t("form:Full_name")} <span className="text-danger">*</span>
						</label>
						<div class="invalid-feedback">{formErrors?.username}</div>
					</div>
					<div className="mb-2 col-6 form-floating">
						<input
							type="number"
							class={`form-control ${
								formErrors?.phoneNumber ? "is-invalid" : "is-valid"
							}`}
							id="phoneInput"
							name="phoneNumber"
							value={formInputs.phoneNumber}
							onChange={(e) => handleChange(e)}
							placeholder="Phone Number"
							style={{ borderRadius: "20px" }}
						/>
						<label for="phoneInput" style={{ left: "10px" }}>
							{t("form:Phone_number")} <span className="text-danger">*</span>
						</label>
						<div class="invalid-feedback">{formErrors?.phoneNumber}</div>
					</div>
					<div className="mb-2 col-6 form-floating">
						<input
							type="email"
							class={`form-control ${
								formErrors?.email ? "is-invalid" : "is-valid"
							}`}
							id="emailInput"
							name="email"
							value={formInputs.email}
							onChange={(e) => handleChange(e)}
							placeholder="Email"
							style={{ borderRadius: "20px" }}
						/>
						<label for="emailInput" style={{ left: "10px" }}>
							{t("form:Email")}
						</label>
						<div class="invalid-feedback">{formErrors?.email}</div>
					</div>
					<div className="mb-2 col-6 form-floating">
						<select
							style={{ borderRadius: "20px" }}
							value={formInputs.city}
							onChange={(e) => handleChange(e)}
							class="form-select"
							id="CityInput"
							name="city"
							aria-label="Floating label select example"
						>
							<option value="Ag">{t("cities:Ashgabat")}</option>
							<option value="Mr">{t("cities:Mary")}</option>
							<option value="Ah">{t("cities:Ahal")}</option>
							<option value="Dz">{t("cities:Dasoguz")}</option>
							<option value="Bl">{t("cities:Balkan")}</option>
							<option value="Lb">{t("cities:Lebap")}</option>
						</select>
						<label for="CityInput" style={{ left: "10px" }}>
							{t("form:Choose_city")}
						</label>
					</div>
					<div className="mb-2 col-12 form-floating">
						<textarea
							class={`form-control ${
								formErrors?.address ? "is-invalid" : "is-valid"
							}`}
							placeholder="Address"
							value={formInputs.address}
							onChange={(e) => handleChange(e)}
							id="floatingTextarea"
							name="address"
							style={{ borderRadius: "20px", height: "65px" }}
						></textarea>
						<label for="floatingTextarea" style={{ left: "10px" }}>
							{t("form:Address")} <span className="text-danger">*</span>
						</label>
						<div class="invalid-feedback">{formErrors?.address}</div>
					</div>

					<button
						type="submit"
						disabled={!isEmpty(formErrors)}
						className="btn btn-primary mt-4 mx-auto py-2"
						style={{ borderRadius: "20px", maxWidth: "400px" }}
					>
						{submitted ? (
							<div class="spinner-border text-white" role="status">
								<span class="visually-hidden">{t("common:loading")}...</span>
							</div>
						) : (
							t("account:update")
						)}
					</button>
				</form>
			</div>
			{/* Order Form End */}
		</div>
	);
}

export default ProfileTab;
